import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom"; // useLocation is used to access passed search term from state
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css"; // Import Toastify CSS
import manualIco from "../assets/images/manual_transmission.svg";
import autoIco from "../assets/images/Automatic_transmission.svg";

function SearchForm() {
  const [isChecked, setIsChecked] = useState(false);
  const [inputValue, setInputValue] = useState(""); // State to store input value
  const navigate = useNavigate(); // Initialize useNavigate for redirection
  const location = useLocation(); // Get the location object to access passed state

  // Check if a search term is passed to pre-fill the input
  useEffect(() => {
    if (location.state?.searchTerm) {
      setInputValue(location.state.searchTerm); // Set input field with the search term
    }
  }, [location.state?.searchTerm]);

  const handleToggle = () => {
    setIsChecked(!isChecked);
  };

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault(); // Prevent the form from refreshing the page
    if (inputValue.trim() !== "") {
      navigate("/search", { state: { searchTerm: inputValue } }); // Pass the input value via state
    } else {
      toast.error("Please enter a location before searching", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      handleSearchSubmit(e); // Trigger search on Enter key press
    }
  };

  return (
    <form className="SearchForm" onSubmit={handleSearchSubmit}>
      <div className="PageBanner__formWrpBottom ">
        <div className="toggle-switch">
          <input
            type="checkbox"
            id="toggle-switch"
            checked={isChecked}
            onChange={handleToggle}
          />
          <label htmlFor="toggle-switch" className="slider">
            <span className="label-manual">
              <img src={manualIco} alt="Manual" /> Manual
            </span>
            <span className="label-auto">
              <img src={autoIco} alt="Auto" /> Auto
            </span>
          </label>
        </div>
        <input
          type="text"
          placeholder="Enter your Suburb here..."
          value={inputValue}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown} // Handle Enter key press
        />
        <button type="submit" className="btn btn-white">
          Search
        </button>
      </div>
      <ToastContainer />
    </form>
  );
}

export default SearchForm;
