import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import { Link } from "react-router-dom";

function DriveTestPackage({
  contentData,
  introData,
  imageSrc,
  buttonClass,
  themeColor,
}) {
  // Check if listing content already contains price and button link
  const hasPrice = /<div class="price">/.test(contentData.paragraph);
  const hasButton = /<a class="btn/.test(contentData.paragraph);

  return (
    <div className="DriveTest" style={{ backgroundColor: themeColor }}>
      <Container>
        <Row>
          <Col lg={12}>
            <SecIntro
              titleStart={introData.titleStart}
              highlight={introData.highlight}
              titleEnd={introData.titleEnd}
              theme={introData.theme}
            />
            <div className="DriveTest__wrp">
              <div className="DriveTest__left">
                <div className="d-flex flex-column">
                  <div
                    className="DriveTest__content"
                    dangerouslySetInnerHTML={{ __html: contentData.paragraph }}
                  />
                  
                  {/* Conditionally render price if not present in contentData.paragraph */}
                  {!hasPrice && (
                    <div className="price">
                      <h4>{contentData.price}</h4>
                    </div>
                  )}

                  {/* Conditionally render button if not present in contentData.paragraph */}
                  {!hasButton && (
                    <Link to="/" className={`btn ${buttonClass}`}>
                      {contentData.buttonName}
                    </Link>
                  )}
                </div>
              </div>
              <div className="DriveTest__right">
                <img src={imageSrc} alt="Driving Test" />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default DriveTestPackage;
